<template>
  <div id="main-app" class="transparent">
      <div id="steps" class="row ">
        <div class="col-3">
          <button class="btn btn-success rounded-circle" style='cursor: pointer;min-width: 35px' @click="goback('formulario')">1</button>
          <a style='cursor: pointer;' @click="goback('formulario')"><span class="step-text center" >{{multilanguage.selectdestinationdate[language]}}</span></a>
        </div>
        <div class="col-3">
          <button class="btn btn-primary rounded-circle"  style='cursor: default;min-width: 35px'>2</button>
          <span class="step-text center" >{{multilanguage.selectexcursion[language]}}</span>
        </div>
        <div class="col-3">
          <button class="btn btn-dark rounded-circle" disabled="disabled" style='cursor: default;min-width: 35px'>3</button>
          <span class="step-text center" >{{multilanguage.configureexcursion[language]}}</span>
        </div>
        <div class="col-3">
          <button class="btn btn-dark rounded-circle" disabled="disabled" style='cursor: default;min-width: 35px'>4</button>
          <span class="step-text center" >{{multilanguage.fillcheckbook[language]}}</span>
        </div>
      </div>
    <div
      <div class="row">
        <div class="col-sm-12 col-md-6 col-lg-3">
          <div class="">
            <div v-if="(dispo && dispo.labels && dispo.labels.length > 0 ) || (maxPrice >=0 && minPrice >=0 && maxPrice !== minPrice)" >
              <div class="d-lg-none" style="margin-top:20px;">
                  <div class="custom-control custom-switch">
                    <input type="checkbox" @click="hidefilter ? hidefilter=false : hidefilter=true" class="custom-control-input" id="customSwitch1">
                    <label class="custom-control-label" for="customSwitch1">{{multilanguage.filter[language]}}</label>
                  </div>
              </div>

              <div class="filter" v-bind:class="{ 'd-none d-lg-block': hidefilter }">
                <div class="title-filter">{{multilanguage.filterby[language]}}</div>

                <div v-if="dispo && dispo.labels && dispo.labels.length > 0" style="padding-bottom: 20px ;">
                  <div class="subtitle-filter">{{multilanguage.type[language]}}</div>
                  <div v-for="(types,index) in dispo.labels ">
                    <div class="custom-control custom-checkbox">
                    <input :id="'ttype'+index" type="checkbox" class="custom-control-input" checked="" v-model="labels[index]"/>
                    <label class="custom-control-label" :for="'ttype'+index"><span>{{types.name}}</span></label>
                    </div>

                  </div>
                </div>
                <div v-if=" (maxPrice >=0 && minPrice >=0 && (maxPrice !== minPrice))">
                  <div class="subtitle">{{multilanguage.price[language]}}</div>
                  <div class="row">
                    <div class="col-12 ">
                      <div class="pricerange">
                        <div id="test-slider" class="" v-model="pricesrange"></div>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="center ">
                  <button class="btn btn-primary center" @click="resetfilter()">{{multilanguage.resetfilter[language]}}</button>
                </div>
              </div>
            </div>
          </div>

            <div class="  d-none d-lg-block">
            <div id="resume-last"class=" resume " >

                <div class="title-resume">{{multilanguage.summary[language]}}</div>
                <div style="margin-bottom:0.5em;" class="valign-wrapper"><i
                  class="material-icons  " style="margin-right:0.5em;">date_range</i><span><b
                  style="margin-right:0.5em;"> {{multilanguage.checkin[language]}}: </b> {{this.$route.query.entrada}}</span>
                </div>

                <div style="margin-bottom:0.5em;" class="valign-wrapper"><i
                  class="material-icons " style="margin-right:0.5em;">location_on</i><span><b
                  style="margin-right:0.5em;">{{multilanguage.destination[language]}}:</b> {{this.$route.query.destinoname}}</span>
                </div>
                <div style="margin-top:1.5em; center">
                  <button class="btn btn-primary" @click="changeParameters()">{{multilanguage.changeoptions[language]}}</button>
                </div>

              </div>
            </div>
        </div>
        <div v-if="loader" class="col-sm-12 col-md-12 col-lg-9 mt-5">
            <h4>{{multilanguage.gettingresources[language]}}</h4>
            <div class="progress">
              <div class="progress-bar progress-bar-striped progress-bar-animated" role="progressbar"  style="width: 100%" ></div>
            </div>
        </div>

        <div v-if="!loader && dispo" class="col-sm-12 col-md-12 col-lg-9 mt-5" >
            <div v-if="dispo.availableActivities && dispo.availableActivities.length>0">

                <div class="row grid">
                  <div v-for="(act, index) in dispo.availableActivities" class="col-sm-12 col-md-6 col-lg-6  col-xl-6" style="margin-left: 0em" v-bind:key="index">
                    <a v-on:click="abrirdetalle(act.activityId,act.name)" style='cursor: pointer'>
                      <div class="card hoverable" style="height: 95%;margin-top:0px;width:100%">
                        <div class="card-image">
                          <!-- añadido i-->
                          <template v-if="act.bestDeal">
                            <div class="card-price">
                              <div v-if="act.bestDeal.offer !== true">
                                {{ act.bestDeal.retailPrice.value }}
                                {{act.bestDeal.retailPrice.currencyIsoCode}}
                              </div>
                              <div v-else>
                                <div class="offer-title">
                                  <div class="valign-wrapper">
                                    {{multilanguage.offerprice[language]}}
                                    <i
                                      class="material-icons tooltipped"
                                      data-position="bottom"
                                      :data-tooltip="act.bestDeal.offerText"
                                      style="cursor: pointer;"
                                    >new_releases</i>
                                  </div>
                                </div>
                                <div class="right">
                                  <span
                                    v-if="act.bestDeal.beforeOfferPrice"
                                    class="oldprice"
                                  >{{act.bestDeal.beforeOfferPrice.value}}</span>
                                  <span class="offer-price">
                                    {{ act.bestDeal.retailPrice.value }}
                                    {{act.bestDeal.retailPrice.currencyIsoCode}}
                                  </span>
                                </div>
                                <!-- <div class="left offer-text">{{act.bestDeal.offerText}}</div>-->
                              </div>
                            </div>
                          </template>
                          <div
                            v-else
                            class="card-price nodispo"
                          >{{multilanguage.noavailablein[language]}}</div>
                          <!-- añadido f-->
                        <template v-if="act.image">
                          <img class=" card-img-top "  :src="act.image" :alt="act.name">
                        </template>
                        <template v-else>
                          <img class=" card-img-top "  :src="noimage" :alt="act.name" >
                        </template>
</div>
                        <div class="card-body my-card-body">
                          <div class="card-title my-card-title">{{act.name}}</div>
                          <p class="card-text my-card-text">{{act.description}}</p>
                         
                        </div>
                      </div>
                    </a>
                  </div>
                </div>
              <!-- with cards-->
            </div>

            <div v-else class="col-sm-12 col-md-12 col-lg-8 mt-5 "  style=" margin-top: 48px;">
              <h4>{{multilanguage.noresults[language]}}</h4>
            </div>
      </div>
      </div>
  </div>
</template>

<script>

import axios from 'axios'
import helper from '@/script/helper';
import noUiSlider from 'nouislider'
import 'nouislider/distribute/nouislider.css'
import wNumb from 'wnumb'

var slider
var globallanguage = 'es'
if (globaldata.language != null && globaldata.language != '') {
  globallanguage = globaldata.language
}
const langfile = require('../../assets/lang/content.json')
const noimage = require('../../assets/noimage.jpg')

var iswhitebrand = false
if (globaldata.whitebrandexcursion !== null && globaldata.whitebrandexcursion !== '') {
  iswhitebrand = true
}
export default {
  name: 'ExcursionDispo',
  data: function () {
    return {
      dispo: null,
      showwhite: iswhitebrand,
      maxPrice: null,
      minPrice: null,
      loader: null,
      hidefilter: true,
      language: globaldata.language,
      multilanguage: langfile.multilanguage,
      pricesrange: null,
      labels: []
    }
  },
  watch: {
    labels: function (val) {
      this.fiterresults()
    }
  },
  methods: {
    goback (page) {
      this.$router.push({name: 'main',
        query: {
          destino: this.$route.query.destino,
          entrada: this.$route.query.entrada,
          destinoname:this.$route.query.destinoname  }})
    },
    changeParameters () {
      this.$router.push({name: 'main',
        query: {
          destino: this.$route.query.destino,
          entrada: this.$route.query.entrada,
          destinoname:this.$route.query.destinoname  }})
    },
    resetfilter () {
      var _this = this
      var d = this.$data

      if (d.maxPrice >= 0 && d.minPrice >= 0 && d.maxPrice !== d.minPrice) {
        slider.noUiSlider.reset()
      }
      _this.getAvailability()
    },
    fiterresults () {
      var d = this.$data
      var _this = this
      var prices = [null, null]
      if (d.maxPrice >= 0 && d.minPrice >= 0 && d.maxPrice !== d.minPrice) {
        prices = slider.noUiSlider.get()
      }
      var selectedLables = ''
      for (var i = 0; i < d.labels.length; i++) {
        if (d.labels[i]) {
          selectedLables = selectedLables + d.dispo.labels[i].id + ','
        }
      }
      axios({
        method: 'get',
        url: globaldata.baseurl + '/activity/filter',
        params: {
          resourceid: this.$route.query.destino,
          start: this.$route.query.entrada ? this.$route.query.entrada.replace(/-/g, '') : null,
          language: this.language,
          minprice: prices[0],
          maxprice: prices[1],
          labels: selectedLables === '' ? null : selectedLables
        }
      })
        .then(function (response) {
          d.loader = null
          d.dispo = response.data

        })
        .catch(function (error) {
          d.loader = null
          d.dispo = null
          console.log(error)
        })
    },
    abrirdetalle (e, name) {
      this.deletetooltips()
      this.$router.push({
        name: 'excursion_rates',
        query: {
          key: e,
          entrada: this.$route.query.entrada,
          destinoname: this.$route.query.destinoname,
          destino: this.$route.query.destino,
          excursionname: name
        }
      })
    },
    abrirficha (k) {
      this.$router.push({name: 'excursion_ficha', query: {hotelkey: k}})
    },
    deletetooltips () {
      var element = document.getElementsByClassName('material-tooltip'), index
      for (index = element.length - 1; index >= 0; index--) {
        element[index].parentNode.removeChild(element[index])
      }
    },

    getDatafromLocator () {
      var d = this.$data
      var _this = this
      d.loader = true
      axios({
        method: 'get',
        url: globaldata.baseurl + '/commons/fromlocator/' + globaldata.localizador,
        params: {}
      })
        .then(function (response) {
          setTimeout(function () {
            d.loader = null
            _this.$route.query.destino = response.data.destination
            _this.$route.query.destinoname = response.data.destinationName
            _this.$route.query.entrada = response.data.checkin.toString().substr(0, 4) + '-' + response.data.checkin.toString().substr(4, 2) + '-' + response.data.checkin.toString().substr(6, 2)

            _this.$route.query.ocupacion = globallanguage
            if (globaldata.whitebrandexcursion !== null && globaldata.whitebrandexcursion !== '') {
              _this.$router.push({
                name: 'excursion_rates',
                query: {

                  key: globaldata.whitebrandexcursion,
                  entrada: _this.$route.query.entrada

                }
              })
            } else {
              _this.getAvailability()
            }
          })
        })
        .catch(function (error) {
          d.loader = null
          d.dispo = null
          console.log(error)
        })
    },
    getAvailability () {
      var d = this.$data
      var _this = this
      d.loader = true

      axios({
        method: 'get',
        url: globaldata.baseurl + '/activity/available',
        params: {
          resorts: this.$route.query.destino,
          start: this.$route.query.entrada ? this.$route.query.entrada.replace(/-/g, '') : null,

          language: this.$route.query.idioma
        }
      })
        .then(function (response) {
          d.loader = false
          d.dispo = response.data

          for (var i = 0; i < d.dispo.labels.length; i++) {
            d.labels[i] = false
          }

          d.minPrice = d.dispo.minPrice
          d.maxPrice = d.dispo.maxPrice

          setTimeout(function () {
            if (d.maxPrice >= 0 && d.minPrice >= 0 && d.maxPrice !== d.minPrice) {
              var step = 10

              if (((d.maxPrice - d.minPrice) < 50) && ((d.maxPrice - d.minPrice) >= 20)) {
                step = 5
              } else if (d.maxPrice !== d.minPrice < 20) {
                step = 1
              }

              slider = document.getElementById('test-slider')

              if (!slider.noUiSlider) {
                noUiSlider.create(slider, {
                  start: [d.minPrice === 0 ? 0 : d.minPrice - 1,
                    d.maxPrice + 1],
                  connect: true,
                  step: step,
                  tooltips: [true, true],
                  pips: {
                    mode: 'count',
                    values: 6,
                    density: 4
                  },
                  orientation: 'horizontal', // 'horizontal' or 'vertical'
                  range: {
                    'min': d.minPrice === 0 ? 0 : d.minPrice - 1,
                    'max': d.maxPrice === 0 ? 0 : d.maxPrice + 1
                  },
                  format: wNumb({
                    decimals: 0
                  })
                })
                slider.noUiSlider.on('change', function (values, handle) {
                  _this.fiterresults()
                })
              }
            }


          })
        })
        .catch(function (error) {
          d.loader = false
          d.dispo = null
          console.log(error)
        })
    }
  },
  mounted () {
    
    var _this = this
    var d = this.$data
    d.loader = true

    if (globaldata.localizador && !this.$route.query.destino) {
      console.log('entra por locator')
      _this.getDatafromLocator()
    } else {
      _this.getAvailability()
    }
    helper.scrollToId("main-app");
  }
}
</script>

