            <label for="destino">{{multilanguage.destination[language]}} *</label>
            <select id="destino" class="form-control " v-model="destobject" style='cursor: pointer' >
              <template v-for="pais in portfolio.countries">

                <option :key="pais.resourceId" :value="pais"  :selected="pais.resourceId === destobject.resourceId">{{ pais.name[language]}}</option>
                <template v-for="prov in pais.states">
                  <option v-if="prov.name" :value="prov" :key="prov.resourceId" >&emsp;{{ prov.name[language] }}</option>
                  <template v-for="loc in prov.cities">
                    <option v-if="loc.name" :key="loc.resourceId" :value="loc" >&emsp;&emsp;{{ loc.name[language] }}</option>

                  </template>

                </template>
              </template>
            </select>
            <div class="invalid-feedback">
              <span id="destinoaux-msg"> </span>
            </div>
          </div>
          <div v-else class="d-flex justify-content-center mt-4">
            <div class="spinner-border text-primary" role="status">
              <span class="sr-only">Loading...</span>
            </div>
          </div>
        </div>
        </template>
        <template v-else>
          <div class="col-sm-12 col-md-12 col-lg-3 col-xl-3">
            <div v-if="portfolio" class="form-group">
              <label class="active" for="destino" data-error="wrong" data-success="right">{{multilanguage.destination[language]}}
                *</label>
              <input type="text" id="destino" class="bg-white validate form-control" readonly
                     v-model="destobject.name[language]" v-on:keyup="elegirdestino('destino')" v-on:focus="elegirdestino('destino')"
                     style='cursor: pointer'>
              <div class="invalid-feedback">
                <span id="destino-msg" > </span>
              </div>
            </div>
          </div>
        </template>
        <div class="col-sm-12 col-md-12 col-lg-3 col-xl-3">
            <div class="form-group"  >
              <label  class="active" data-error="wrong"
                      data-success="right">{{multilanguage.startdate[language]}} *</label>
              <v-date-picker
                mode='single'
                v-model='entrada'
                :format='formats'
                :input-props='fechaentradaclass'>
              </v-date-picker>
              <input id="checkindates" class="validator" style="display:none;">
              <div class="invalid-feedback">
                <span id="checkindates-msg" > </span>
              </div>
            </div>
        </div>

        <div class="col-sm-12 col-md-12 col-lg-3 col-xl-3 mt-3">
          <div class="form-group ">
            <div class=" occupation-container  button-container">
              <button type="button" class="btn btn-primary" v-on:click="buscar">{{multilanguage.search[language]}}</button>
            </div>
          </div>
        </div>
      </div>
      <div class="row mt-3">
        <div class="col-sm-12">
          <label class="text-danger float-left">{{multilanguage.requiredfield[language]}}</label>
        </div>
      </div>

    </form>



  <!-- Modal Structure -->

  <div class="modal" id="modal1" tabindex="-1" role="dialog">
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">{{multilanguage.choosedest[language]}}</h5>

          <button type="button" class="close" @click="closemodal()" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div >
            <div>
              <div class="form-group  mt-3">
                <label for="busqueda" data-error="wrong"
                       data-success="right">{{multilanguage.searchtext[language]}}</label>
                <input id="busqueda" type="text" class="validate form-control" v-model="busqueda"
                       v-on:keyup="buscardestino">

              </div>
              <div class="destinocontent">
                <div v-for="(c,index2) in coincidencias.matches" class="mb-2" v-bind:key="index2">
                  <h5 class="text-primary"><a v-on:click="seleccionardestino(c.name,c.resourceId)" :data-key="c.resourceId" :data-name="c.name"
                                              style='cursor: pointer'>{{ c.name }}</a>
                  </h5>
                  <span>{{ c.description }}</span>
                </div>
              </div>

            </div>
          </div>
          <!--   <ul class="nav nav-tabs" id="tabsdestino" role="tablist">
               <li class="nav-item">
                 <a  class="nav-link active" id="elegir-tab " data-toggle="tab"  role="tab" aria-controls="elegir" aria-selected="true"  href="#elegir">
                   <template v-if="selectiontype === 'origen'">{{multilanguage.chooseorig[language]}}</template>
                   <template v-else>{{multilanguage.choosedest[language]}}</template>
                 </a>
               </li>
               <li class="nav-item">
                 <a  class="nav-link" id="buscar-tab " data-toggle="tab"  role="tab" aria-controls="elegir" aria-selected="true"  href="#buscar">
                   <template v-if="selectiontype === 'origen'">{{multilanguage.searchorig[language]}}</template>
                   <template v-else>{{multilanguage.searchdest[language]}}</template></a>
                 </a>
               </li>
             </ul>
             <div class="tab-content" id="tabsdestinoContent">
               <div class="tab-pane fade show active" id="elegir" role="tabpanel" aria-labelledby="elegir-tab">
                 <div v-if="destination" class="list-group">
                     <a v-for="(dest, index1) in destination"  v-if="dest.name[language]" class="list-group-item list-group-item-action" v-on:click="seleccionardestino(dest.name[language],dest.resourceId)" :data-key="dest.resourceId"
                                                   :data-name="dest.name[language]" style='cursor: pointer' v-bind:key="index1">{{
                       dest.name[language] }} </a>
                 </div>
               </div>
               <div class="tab-pane fade" id="buscar" role="tabpanel" aria-labelledby="buscar-tab">
                 <div>
                   <div class="form-group  mt-3">
                     <label for="busqueda" data-error="wrong"
                            data-success="right">{{multilanguage.searchtext[language]}}</label>
                     <input id="busqueda" type="text" class="validate form-control" v-model="busqueda"
                            v-on:keyup="buscardestino">

                   </div>

                   <div v-for="(c,index2) in coincidencias.matches" class="mb-2" v-bind:key="index2">
                     <h5 class="text-primary"><a v-on:click="seleccionardestino(c.name,c.resourceId)" :data-key="c.resourceId" :data-name="c.name"
                            style='cursor: pointer'>{{ c.name }}</a>
                     </h5>
                     <span>{{ c.description }}</span>
                   </div>

                 </div>
               </div>

             </div>-->

        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-dismiss="modal" @click="closemodal()">Close</button>
        </div>
      </div>
    </div>
  </div>
</div>

</template>


<script>
import axios from 'axios'

var globallanguage = 'es'
if (globaldata.language != null && globaldata.language != '') {
  globallanguage = globaldata.language
}

const langfile = require('../../assets/lang/content.json')

var opciones

/*modal1 var modal1 */

var today = new Date();
var tomorrow = new Date();
tomorrow.setDate(tomorrow.getDate() + 1)

export default {
  name: 'Formulario',
  data: function () {
    return {
      excursionshortdestinations: globaldata.excursionshortdestinations === true ? true:false,
      destobject: {
        resourceId: '',
        name: {
          display: ''
        }

      },
      entrada: this.getInitialDate('entrada'),
      portfolio: null,
      busqueda: null,
      coincidencias: {
        matches: []
      },
      language: globaldata.language,
      multilanguage: langfile.multilanguage,
      fechaentradaclass: {
        class: 'form-control'
      },
      formats: {
        title: 'MMMM YYYY',
        weekdays: 'W',
        navMonths: 'MMM',
        input: ['L', 'YYYY-MM-DD', 'YYYY/MM/DD'], // Only for `v-date-picker`
        dayPopover: 'L', // Only for `v-date-picker`
        data: ['L', 'YYYY-MM-DD', 'YYYY/MM/DD'] // For attribute dates
      }
    }
  },
  methods: {

    elegirdestino: function (selectype) {
      this.$data.selectiontype = selectype
      $(function () {
        $('#modal1').show()
        $('#busqueda').focus()
      })
    },
    closemodal: function (e) {

      var d = this.$data
      d.busqueda = null
      d.coincidencias = { matches: [] }

      $(function () {
        $('#modal1').toggle()
      })
    },
    buscardestino: function (e) {
      var especial = false
      especial |= e.keyCode === 37
      especial |= e.keyCode === 38
      especial |= e.keyCode === 39
      especial |= e.keyCode === 40

      if (!especial) {
        var coins = this.$data.coincidencias

        axios({
          method: 'get',
          url: globaldata.baseurl + '/commons/search',
          params: {
            language: globaldata.language,
            query: this.$data.busqueda
          }
        })
          .then(function (response) {
            coins.matches = response.data.matches
          })
          .catch(function (error) {
            console.log(error)
          })
      }
    },
    seleccionardestino: function (name, resourceId) {

      this.$data.destobject.name[this.$data.language] = name
      this.$data.destobject.resourceId = resourceId


      this.closemodal()
    },
    buscardestinox: function (e) {
      var especial = false
      especial |= e.keyCode === 37
      especial |= e.keyCode === 38
      especial |= e.keyCode === 39
      especial |= e.keyCode === 40

      if (!especial) {
        axios({
          method: 'get',
          url: globaldata.baseurl + '/commons/search',
          params: {
            language: globaldata.language,
            query: this.$data.destobject.name.display
          }
        })
          .then(function (response) {
            var opts = {}
            opciones = response.data.matches
            for (var m in opciones) {
              opts[opciones[m].name] = null
            }

            instance.updateData(opts)
          })
          .catch(function (error) {
            console.log(error)
          })
      }
    },
    buscar: function (e) {
      var d = this.$data
      e.preventDefault()

      if (this.checkForm()){
        this.$router.push({
          name: 'excursion_dispo',
          query: {
            destino: this.$data.destobject.resourceId,
            entrada: this.formatDate(this.$data.entrada),
            idioma: this.$data.idioma,
            destinoname:  this.$data.destobject.name[d.language]} })
      }
    },
    formatDate: function (date) {
      var d = new Date(date)
      var month = '' + (d.getMonth() + 1)
      var day = '' + d.getDate()
      var year = d.getFullYear()


      if (month.length < 2) month = '0' + month;
      if (day.length < 2) day = '0' + day;

      return [year, month, day].join('-');
    },
    checkForm: function (e) {
      var existsErrors = false
      this.cleanErrors()
      
      if (!this.destobject.resourceId || this.destobject.resourceId === null || this.destobject.resourceId === '') {
        this.setValidationError('destino', 'destinoaux-msg', this.multilanguage.errordestinationempty[this.language])
        existsErrors = true
      
      }
      if (this.entrada === null || this.entrada === '') {
        this.setValidationError('checkindates', 'checkindates-msg', this.multilanguage.errorcheckinempty[this.language])
        existsErrors = true
      
      }else{

        var fentrada = new Date(this.entrada)
        var today = new Date()

        fentrada.setHours(0,0,0,0);
        today.setHours(0,0,0,0);


        if (fentrada.getTime() < today.getTime()) {
          this.setValidationError('checkindates', 'checkindates-msg', this.multilanguage.errorcheckintoday[this.language])
          existsErrors = true
      
        }

      }
      if (existsErrors) {
        return false
      }
      return true
    },
    setValidationError (field, msgField, msg) {
      var d = this.$data

      if (field === 'checkindates') {
        d.fechaentradaclass.class = 'form-control is-invalid'
      }
      var element = document.getElementById(field)
      element.classList.add('is-invalid')
      element.classList.add('form-control')
      element.focus()

      if (msgField !== null && msgField !== ' ') {
        var elementmsg = document.getElementById(msgField)

        elementmsg.innerHTML = msg
      }

    },
     getInitialDate: function(dt){
        let maindata = JSON.parse(localStorage.getItem("qtse_maindatastorage"));
        if (dt === 'entrada')
        {    
          if(this.$route.query.entrada){
            return new Date(this.$route.query.entrada)
          }else{
            if(maindata && maindata.startdate ){
                return new Date(maindata.startdate)
            }
            else
            {
              return today;
            }
          }
        }
        if (dt === 'salida'){
            if(this.$route.query.salida){
              return new Date(this.$route.query.salida)
            }else{
              if(maindata && maindata.enddate ){
                return new Date(maindata.enddate)
              }
              else
              {
                return tomorrow;
              }
          }
        }
          
      },
    cleanErrors: function () {
      var d = this.$data

      var element = document.getElementById('destino')
      element.setAttribute('class', 'form-control bg-white')

      element = document.getElementById('checkindates')
      d.fechaentradaclass.class = 'form-control'
      element.setAttribute('class', 'form-control bg-white')


    }
  },
  mounted () {

    if( globaldata.whitebrandexcursion && globaldata.whitebrandexcursion !== null && globaldata.whitebrandexcursion !== '')
    {
      this.$router.push({name: 'excursion_rates',
        query: {
          key: globaldata.whitebrandexcursion,
          entrada:  this.formatDate(today),
          idioma: this.$data.language
        }
      })
    }
    var d = this.$data
    var _this = this

    axios({
      method: 'get',
      url: globaldata.baseurl + '/commons/portfolio'
    })
      .then(function (response) {

        d.portfolio = response.data
        if (_this.$route.query.destinoname && _this.$route.query.destino) {
          if(d.excursionshortdestinations){
          for (var i = 0; i < d.portfolio.countries.length; i++) {
            d.portfolio.countries[i].name.display = d.portfolio.countries[i].name[globallanguage]
            if (_this.$route.query.destino === d.portfolio.countries[i].resourceId){
              d.destobject = d.portfolio.countries[i]
            }

            for (var j = 0; j < d.portfolio.countries[i].states.length; j++) {
              if (d.portfolio.countries[i].states[j].name) {
                d.portfolio.countries[i].states[j].name.display = d.portfolio.countries[i].states[j].name[globallanguage]
              }
              if (_this.$route.query.destino === d.portfolio.countries[i].states[j].resourceId){
                d.destobject = d.portfolio.countries[i].states[j]
              }
              for (var k = 0; k < d.portfolio.countries[i].states[j].cities.length; k++) {
                d.portfolio.countries[i].states[j].cities[k].name.display = d.portfolio.countries[i].states[j].cities[k].name[globallanguage]
                if (_this.$route.query.destino === d.portfolio.countries[i].states[j].cities[k].resourceId){
                  d.destobject = d.portfolio.countries[i].states[j].cities[k]
                }
              }
            }
          }
          }else{
            d.destobject.resourceId = _this.$route.query.destino
            d.destobject.name[d.language] = _this.$route.query.destinoname
          }
        }else{
          d.destobject = d.portfolio.countries[0].states[0]
        }




      })
      .catch(function (error) {
        d.portfolio = '1' //Stops spinner
        console.log(error)
      })

  },
}
</script>

<style scoped>

  .occupation-container{
    padding-top: 1em;
  }
  .destinocontent {
    max-height: 50vh;
    overflow-y: auto;
  }
  .modal-dialog {
    margin-top: 15%;
  }
</style>
